<template lang="html">
  <v-container
      class="pa-2"
      fluid
    >
  <v-row>
    <v-col v-for="({ name, repeat, display, amount, start , type } , i) in trust" :key="i" cols="12" sm="6" md="4">
      <v-card v-if="type === 'simple'" :style="`opacity: ${display+.5}`">
                <v-card-text>
                  <div class="headline mb-2">
                    <v-icon :color="!amount? 'gray' : amount > 0 ? 'green' : 'red'">{{!amount? 'insert_chart' : amount > 0 ? 'trending_up' : 'trending_down'}}</v-icon>
                    {{name}}
                    <v-icon @click="visibility(i)">{{ display ? 'visibility_off' : 'visibility'}}</v-icon>
                    <v-icon @click="$store.commit('remove', i)" style="float:right">close</v-icon>
                  </div>
                  <p>платеж от {{ start | toTime }}</p>
                  <v-text-field :disabled="!display" @input="changeCard($event, 'amount', i )" :value="amount" label="amount" />
                  <div>
                    <v-select
                        clearable
                        :disabled="!display"
                        :value="repeat"
                        @change="changeCard($event, 'repeat', i )"
                        :items="$store.getters.repeats"
                        menu-props="auto"
                        label="Повтор платежа"
                        hide-details
                        prepend-icon="date_range"
                        single-line
                      ></v-select>
                  </div>
                </v-card-text>

                <v-card-actions>
                </v-card-actions>
              </v-card>
          <v-card :style="`opacity: ${display+.5}`" v-else>
            <div class="headline mb-2">
              <v-icon :color="!amount? 'gray' : amount > 0 ? 'green' : 'red'">{{!amount? 'insert_chart' : amount > 0 ? 'trending_up' : 'trending_down'}}</v-icon>
              {{name}}
              <v-icon @click="visibility(i)">{{ display ? 'visibility_off' : 'visibility'}}</v-icon>
              <v-icon @click="$store.commit('remove', i)" style="float:right">close</v-icon>
            </div>
            <v-card-text>
              tool
            </v-card-text>

            <v-card-actions>
            </v-card-actions>
          </v-card>
    </v-col>
  </v-row>
  </v-container>
</template>

<script>
import moment from 'moment'

export default {
  computed: {
    trust(){
        return this.$store.state.events
    }
  },
  methods: {
    visibility(i){
      this.$store.commit('display', i )
    },
    changeCard(e, key, index){
      let value = ''

      if (key === 'amount') {
        value = parseInt( e )
        if ( isNaN( value ) )
          return
      } else {
        value = e
      }

      this.$store.commit( 'editevent',
        {value, key, index }
      )
    }
  },
  filters: {
    toTime(value){
      return moment(value).format('DD MMMM YYYY')
    }
  }
  // data: () => ({
  //   trust: new Array(10).fill(0).map((_,i) => ({
  //     name: "test "+i,
  //     display: true,
  //     amount: 100,
  //     start: Date.now(),
  //     repeat: null
  //   }))
  // })
}
</script>

