<template lang="html">
  <div class="select_dates">
    <p>from</p>
    <v-menu
       ref="menuFrom"
       v-model="menuFrom"
       :close-on-content-click="false"
       :return-value.sync="dateFrom"
       transition="scale-transition"
       offset-y
       min-width="290px"
     >
       <template v-slot:activator="{ on }">
         <v-text-field
           v-model="dateFrom"
           label="Picker in menu"
           prepend-icon="event"
           readonly
           v-on="on"
         ></v-text-field>
       </template>
       <v-date-picker v-model="dateFrom" no-title>
         <div class="flex-grow-1"></div>
         <v-btn text color="primary" @click="selectDateCancel">Cancel</v-btn>
         <v-btn text color="primary" @click="selectDate">OK</v-btn>
       </v-date-picker>
     </v-menu>

    <p>to</p>
    <v-menu
       ref="menuTo"
       v-model="menuTo"
       :close-on-content-click="false"
       :return-value.sync="dateTo"
       transition="scale-transition"
       offset-y
       min-width="290px"
     >
       <template v-slot:activator="{ on }">
         <v-text-field
           v-model="dateTo"
           label="Picker in menu"
           prepend-icon="event"
           readonly
           v-on="on"
         ></v-text-field>
       </template>
       <v-date-picker v-model="dateTo" no-title>
         <div class="flex-grow-1"></div>
         <v-btn text color="primary" @click="selectDateCancel">Cancel</v-btn>
         <v-btn :disabled="!canSelect" text color="primary" @click="selectDate('to')">OK</v-btn>
       </v-date-picker>
     </v-menu>
  </div>
</template>

<script>
import moment from 'moment'

export default {
  computed: {
    canSelect(){
        return new Date(this.dateTo).getTime() - new Date(this.dateFrom).getTime() > 0
    }
  },
  methods: {
    selectDateCancel(){
      this.menuTo = false
      this.menuFrom = false
    },
    selectDate( arg ){
      if ( arg === 'to')
        this.$refs.menuTo.save(this.dateTo)
      else
        this.$refs.menuFrom.save(this.dateFrom)

      this.$store.commit('select', {
        from: this.dateFrom,
        to: this.dateTo
      })
    }
  },
  data: () => ({
    dateFrom: new Date().toISOString().substr(0, 10),
    dateTo: moment().add(1,'year').toISOString().substr(0, 10),
    menuFrom: false,
    menuTo: false
  })
}
</script>

<style lang="css">
.select_dates {
  display: flex;
  align-items: center;
  width: 100%;
}
.select_dates p {
  margin: auto 12px;
  font-weight: 600;
}
</style>
